import React, { Component } from "react";
import { observer } from "mobx-react";
import Store from "./Store";
import Styles from "./Styles";
import { List, ListItem } from "material-ui/List";
import Subheader from "material-ui/Subheader";
import { Row, Col } from "react-grid-system";
import RaisedButton from "material-ui/RaisedButton";
@observer
class Results extends Component {
  render() {
    return (
      <div style={Styles.results}>
        <header
          style={{
            borderTopLeftRadius: "8px",
            borderTopRightRadius: "8px",
            background: "#D0D8DD",
            padding: "16px 32px"
          }}>
          <h2
            style={{
              margin: "16px 0"
            }}>
            {Store.searchText}
          </h2>
          <p
            style={{
              color: "#4D616A"
            }}>
            <i>Homo Sapiens</i>
          </p>
        </header>

        <div
          style={{
            padding: "16px 32px 40px",
            fontSize: "18px",
            fontWeight: "300"
          }}>
          <h2
            style={{
              margin: "32px 0"
            }}>
            <small>CELL LINE</small> <br />
            HPSI1013i-yemz_3
            <RaisedButton
              style={{ marginLeft: "32px" }}
              secondary={true}
              label="View"
            />
          </h2>

          <List>
            <ListItem
              primaryText="Banked at EBiSC as WTSIi022-B "
              secondaryText="Banking Status"
            />
            <ListItem
              primaryText="Cambridge BioResource"
              secondaryText="Tissue provider"
            />{" "}
            <ListItem
              primaryText="2014-04-17"
              secondaryText="Date of derivation"
            />{" "}
            <ListItem primaryText="Male" secondaryText="Donor sex" />{" "}
            <ListItem
              primaryText="Skin Tissue"
              secondaryText="Source material"
            />
          </List>
        </div>
      </div>
    );
  }
}
export default Results;



